                        @change="onQtyChange($event, index)"
                      />
                    </div>

                    <div class="cal-total col-sm-2">
                      <h4 class="item-total">
                        ${{ calculateItemPrice(index) }}
                      </h4>
                    </div>

										<button
											v-if="f.existed"
											@click="onGradeClick(f)"
											class="btn btn-primary mt-2"
