</template>

<script>
import axios from "axios";
import { mapState, mapMutations } from "vuex";
export default {
  name: "Cart",

  data() {
    return {
      cartItem: [],
      itemQuantity: [],
      existItem: [],
			showGradeModal: false,
      selectedOrder: {},
      grade: 1,          
      description: ''       
    };
  },

  // created() {
  //     this.getAllCartItem();
  // },

  mounted() {
    this.getAllCartItem();
		this.setTableId();
  },

  computed: {
    ...mapState(["allFoods", "user"]),

    filterFoods: function () {
			console.log("oo", this.allFoods);
			console.log("ww", this.cartItem); // [1,3]
			if (this.existItem.data === undefined) return [];
			console.log("pp", this.existItem.data); // [{ food_id: 1, user_id: 2}];
			const res = this.allFoods
			.filter(f => 
				this.existItem.data.some(item => item.food_id === f.food_id)
			)
			.map(f => {
				const matchedItem = this.existItem.data.find(item => item.food_id === f.food_id);
				return {
					...f,
					user_id: matchedItem.user_id,
					table_id: matchedItem.table_id,
					existed: matchedItem.existed
				};
			});
			console.log("final res", res);
			return res;
		},
  },

  methods: {
    ...mapMutations(["setUser"]),

		onGradeClick: async function(order) {
			console.log("order", order)
      this.selectedOrder = order
      this.grade = 1
      this.description = ''
      this.showGradeModal = true
		},

		submitGrade: async function() {
      try {
        const payload = {
          food_id: this.selectedOrder.food_id,
          user_id: this.selectedOrder.user_id,
          table_id: this.selectedOrder.table_id,
          grade: this.grade,
          description: this.description
        }
        await axios.post('/grade', payload);
        this.showGradeModal = false;
      } catch (err) {
        console.error('Failed to submit grade', err);
      }
    },

    closeModal: function() {
      this.showGradeModal = false
    },

		checkUserExisted: function() {
			let userInStorage = localStorage.getItem("user");
				const parsedUser = JSON.parse(userInStorage);
				if (!this.user && parsedUser !== null) {
					this.setUser(parsedUser); // 若 Vuex 中還沒設好，再同步
				}
		},

    matchID: function (food, cartArray) {
      let temp = "";
      cartArray.forEach((element) => {
        if (parseInt(food.food_id) == element) {
          temp = food;
        }
      });
      return temp;
    },

    calculateItemPrice: function (index) {
      return (
        (parseInt(this.filterFoods[index].food_price) -
          parseInt(this.filterFoods[index].food_discount)) *
        this.itemQuantity[index]
      ).toString();
    },

    calculateSummaryPrice: function () {
      let subtotal = 0;
      let discount = 0;
      let delivery = 15;
      let i = 0;
			console.log("qwqq", this.filterFoods, this.itemQuantity);
			if (this.filterFoods.length === 0) return [subtotal, discount, delivery, 0];
      while (i < this.itemQuantity.length) {
        console.log("qwqqqqq", this.filterFoods[i]);
        if (this.filterFoods[i] === undefined) {
          i = i + 1;
          continue;
        }
        subtotal =
          subtotal +
          parseInt(this.filterFoods[i].food_price) * this.itemQuantity[i];
        discount =
          discount +
          parseInt(this.filterFoods[i].food_discount) * this.itemQuantity[i];
        i = i + 1;
      }
      if (!this.filterFoods.length) {
        delivery = 0;
      }
      let total = subtotal - discount + delivery;
      return [subtotal, discount, delivery, total];
    },

    async onQtyChange(e, i) {
      if (e.target.value < 1) {
        e.target.value = 1;
        this.itemQuantity[i] = 1;
      } else {
        this.itemQuantity[i] = e.target.value;
      }

      let data = {
        user_id: parseInt(this.user.user_id),
        food_id: parseInt(this.cartItem[i]),
        item_qty: this.itemQuantity[i],
      };
      await axios.put("/cartItem/", data);
    },

    async cancelBtn() {
      await axios.delete("/cartItem/" + this.user.user_id);

      this.cartItem = [];
      this.itemQuantity = [];
    },

    checkOutBtn: function () {
      this.$router.push("/checkout");
    },

    async removeBtn(index) {
      await axios.delete(
        "/cartItem/" + this.user.user_id + "/" + this.cartItem[index]
      );

      this.cartItem.splice(index, 1);
      this.itemQuantity.splice(index, 1);
    },

    async getAllCartItem() {
			this.checkUserExisted();
      if (this.user) {
        const existItem = await axios.get("/cartItem/" + this.user.username);
				this.existItem = existItem;
        this.existItem.data.forEach((element) => {
          this.cartItem.push(element.food_id);
          this.itemQuantity.push(element.item_qty);
        });
				for (let i = 0;i < this.existItem.data.length;i++) {
					try {
						await axios.get(`/download/order_${this.existItem.data[i].food_id}_${this.existItem.data[i].user_id}_${this.existItem.data[i].table_id}.png`);
						this.existItem.data[i].existed = true;
					}
					catch(e) {
						console.log("e", e);
					}
				}
      }
    },
  },
};
</script>

